<template>
    <v-container>
        <v-form @submit.prevent = "agregarMascota()">

            <v-text-field 
            v-model="atributos.nombre" 
            :counter="30" 
            label="Nombre de Mascota"
            ></v-text-field>

            <v-text-field 
            v-model="atributos.raza" 
            label="Raza de la  Mascota"
            ></v-text-field>

            <v-select 
            v-model="atributos.especie" 
            :items="especieO" 
            label="Especie de la mascota"
            ></v-select>

                <v-btn 
                color="success" 
                class="mt-4" 
                type="submit">
                Enviar Registro
                </v-btn>

                <v-btn 
                color="error" 
                class="mt-4" 
                >
                Limpiar Los Campos
                </v-btn>
        </v-form>
    </v-container>
</template>

<script>
export default {
    data(){
        return {
            Atributos:[],
            atributos: {
                nombre:"",
                especie:"",
                raza:"",
            },
            especieO:['Gato', 'Perro']
        }
    },
    methods: {
        agregarMascota(){
            console.log(this.atributos);
            if(!this.atributos.nombre) {
                this.$swal('Error!',
                            'Falta Nombre de Mascota!',
                            'error');
            }else if(!this.atributos.especie) {
                this.$swal('Error!',
                            'Falta Especie de Mascota!',
                            'error');
            }else if(!this.atributos.raza) {
                this.$swal('Error!',
                            'Falta Raza de Mascota!',
                            'error');
            }else{
                this.axios
            .post("/nuevo-registro", this.atributos)
            .then((res) => {
                if(!this.atributos.nombre) {
                this.$swal('Success!',
                            'Mascota Agregada Exitosamente!',
                            'success');
                }
                this.Atributos.push(res.data);
                this.atributos.nombre = "";
                this.atributos.especie = "";
                this.atributos.raza = "";
            })
            .catch((e) => {
                console.log(e.response);
                alert("Error en guardar registro");
            })
            }
        }

    }
}


</script>