<template>
    <v-container>
        <v-data-table :headers="headers" :items="desserts" :sort-by="calories" class="elevation-1">
            <template v-slot:top>
                <v-toolbar flat>
                    <v-toolbar-title>MASCOTAS</v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                                New Item
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                                <v-container>
                                    <v-row>
                                        <v-col cols="12" sm="6" md="4">
                                            <v-text-field v-model="editedItem.nombre"
                                                label="Nombre de Mascota"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4">
                                            <v-text-field v-model="editedItem.especie" label="Especie"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4">
                                            <v-text-field v-model="editedItem.raza" label="Raza"></v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue-darken-1" variant="text" @click="close">
                                    Cancelar
                                </v-btn>
                                <v-btn color="blue-darken-1" variant="text" @click="editarMascota(editedItem)">
                                    Modificar
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="dialogDelete" max-width="500px">
                        <v-card>
                            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue-darken-1" variant="text" @click="closeDelete">Cancel</v-btn>
                                <v-btn color="blue-darken-1" variant="text" @click="deleteItemConfirm">OK</v-btn>
                                <v-spacer></v-spacer>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-toolbar>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-icon small class="me-2" @click="editItem(item._id)">
                    mdi-pencil
                </v-icon>
                <v-icon small @click="elinimarMascotas(item._id)">
                    mdi-delete
                </v-icon>
            </template>
            <template v-slot:no-data>
                <v-btn color="primary" @click="initialize">
                    Reset
                </v-btn>
            </template>
        </v-data-table>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            dialog: false,
            dialogDelete: false,
            headers: [
                { text: 'ID de mascota', value: '_id' },
                { text: 'Nombre de mascota', value: 'nombre' },
                { text: 'Especie', value: 'especie' },
                { text: 'Raza', value: 'raza' },
                { text: 'Acciones', value: 'actions', sortable: false },
            ],
            desserts: [],
            editedIndex: -1,
            editedItem: [],
            defaultItem: {

            },
        };
    },

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        },
        dialogDelete(val) {
            val || this.closeDelete()
        },
    },

    created() {
        this.initialize()
        this.listarMascotas()
    },

    methods: {
        initialize() {

        },

        editItem(item) {
            this.dialog = true
            console.log(item);
            this.axios.get(`/buscarParametro/${item}`)
                .then(res => {
                    this.editedItem = res.data;
                })
                .catch(error => {
                console.error('Error:', error);
                    });
        },

        deleteItem(item) {
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.desserts.splice(this.editedIndex, 1)
            this.closeDelete()
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        closeDelete() {
            this.dialogDelete = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        save() {
            if (this.editedIndex > -1) {
                Object.assign(this.desserts[this.editedIndex], this.editedItem)
            } else {
                this.desserts.push(this.editedItem)
            }
            this.close()
        },

        listarMascotas() {
            this.axios.get('/buscarTodo')
                .then((response) => {
                    this.desserts = response.data;
                })
                .catch((e) => {
                    console.log('Error' + e);
                })
        },
        editarMascota(item) {
            this.axios.put(`/actualizar/${item._id}`, item)
                .then(res => {
                    this.$swal('Success!',
                            'Mascota Modificada Exitosamente!',
                            'success');
                    this.close();
                    const index = this.desserts.findIndex(n => n._id === res.data._id)
                    this.desserts[index].nombre = res.data.nombre;
                    this.desserts[index].especie = res.data.especie;
                    this.desserts[index].raza = res.data.raza;
                })
                .catch((e) => {
                    console.log('Error' + e);
                })
        },
        elinimarMascotas(id){
            console.log(id);
            this.axios.delete(`/eliminarParametro/${id}`)
            .then(res => {
                this.$swal('Ok!',
                            'Mascota Eliminada Exitosamente!',
                            'error');
                const index = this.desserts.findIndex(item => item._id === res.data._id)
                this.desserts.splice(index, 1);
                this.editedIndex = this.desserts.indexOf(item);
                this.editedIndex = Object.assign({}, item)
            })
            .catch((e) => {
                    console.log(e.response);
                })
        }
    },
}
</script>


